<template>
	<div class="goods">
		<headerNav title="商品详情"/>

		<van-swipe :autoplay="3000" :height="swipeHeight">
			<van-swipe-item v-for="(item, index) in goodsInfo.ImgList" :key="index">
				<img :src="item.Src" style="width: 100%; height: 100%"/>
			</van-swipe-item>
		</van-swipe>

		<van-cell-group>
			<van-cell>
				<span class="goods-price">¥ {{goodsInfo.SpecialOffer}}</span>
				<span style="color: #999999; font-size: 14px"><s>￥{{goodsInfo.OriginalPrice}}</s></span>
				<div class="goods-title">{{goodsInfo.GoodsName}}</div>
				<div class="goods-subtit">{{goodsInfo.Describe}}</div>
			</van-cell>
		</van-cell-group>

		<van-cell-group class="goods-cell-group">
			<van-cell>
				<template slot="title">
					<span style="margin-right: 10px;">条码</span>
					<span>{{serialString}}</span>
				</template>
			</van-cell>
			<van-cell>
				<template slot="title">
					<span style="margin-right: 10px;">规格</span>
					<span>{{goodsInfo.Specifications}}</span>
				</template>
			</van-cell>
			<van-cell>
				<template slot="title">
					<span style="margin-right: 10px;">装箱数</span>
					<span>{{goodsInfo.PackingNumber}}</span>
				</template>
			</van-cell>
			<van-cell>
				<template slot="title">
					<span style="margin-right: 10px;">整件价</span>
					<span>￥{{goodsInfo.singlePrice}}</span>
				</template>
			</van-cell>
			<van-cell is-link @click="showSku">
				<template slot="title">
					<span style="margin-right: 10px;">已选</span>
					<span>{{specString}}</span>
				</template>
			</van-cell>
		</van-cell-group>

		<van-goods-action>
			<Badge :count="0" overflow-count="99" :offset="[0, 25]" style="width: 30%">
				<van-goods-action-mini-btn icon="cart" @click="toCart">
					购物车
				</van-goods-action-mini-btn>
			</Badge>
			<van-goods-action-big-btn primary @click="showSku">
				加入购物车
			</van-goods-action-big-btn>
		</van-goods-action>

		<van-sku
				v-model="isSkuShow"
				:sku="skuData.sku"
				:goods="skuData.goodsInfo"
				:goods-id="skuData.goodsInfo.id"
				:hide-stock="true"
				:hide-quota-text="true"
				:quota="0"
				:close-on-click-overlay="true"
				@add-cart="onAddCartClick"
		>
			<template slot="sku-actions" slot-scope="props">
				<div class="van-sku-actions">
					<van-button type="primary" :disabled="goodsInfo.Stock <= 0" bottom-action @click="props.skuEventBus.$emit('sku:addCart')">加入购物车</van-button>
				</div>
			</template>

			<template slot="sku-header-price" slot-scope="props">
				<div style="display: flex; flex-direction: column;">
					<div style="color: red;">￥{{goodsInfo.SpecialOffer}}</div>
					<div>
						<span v-if="goodsInfo.Quota !== -1" style="margin-right: 10px; font-size: 12px;">限购：{{goodsInfo.Quota}}件</span>
						<span style="font-size: 12px;">库存：{{goodsInfo.Stock}}件</span>
					</div>
				</div>
			</template>

			<template slot="sku-group" slot-scope="props">
				<div class="van-sku-group-container">
					<div class="van-sku-row" v-for="(items, index) in goodsInfo.ValuesList" :key="index">
						<div class="item-select-title">
							<p>{{specs[goodsInfo.ValuesList[index][0].CategoryType - 1]}}：</p>
						</div>
						<div class="item-select-column">
							<div class="item-select-row">
								<RadioGroup v-model="selectString[index]" type="button" @on-change="select(index)">
									<Radio v-for="(item, index1) in items" :key="index1" :label="item.GoodsValueName"></Radio>
								</RadioGroup>
							</div>
						</div>
					</div>
				</div>
			</template>

			<template slot="sku-stepper" slot-scope="props">
				<div class="van-sku-stepper-stock">
					<div class="van-sku-stepper-container">
						<div class="van-sku__stepper-title">购买数量：</div>
						<div class="van-stepper van-sku__stepper">
							<van-stepper v-model="count" integer :min="1" :max="maxCount" input-width="50px"/>
						</div>
					</div>
				</div>
			</template>
		</van-sku>
	</div>
</template>

<script>
	import {Dialog} from 'vant';

	import headerNav from '../components/header/nav'
	import {mapState, mapActions} from 'vuex';

	export default {
		name: "GoodsDetail",

		components: {
			headerNav
		},

		data() {
			return {
				swipeHeight: null,
				isSkuShow: false,
				maxCount: 10000,
				skuData: {
					sku: {
						tree: [],
						list: [],
						price: '',
					},
					goodsInfo: {
						id: null,
						title: '',
						picture: '',
					}
				},
				selectString: [],
				selectIndex: [],
				specs: ['味道', '颜色', '尺寸'],
				specString: '',
				serialNumbers: [],
				serialString: '',
				count: 1,
			};
		},

		computed: {
			...mapState(['goodsInfo', 'shoppingCart']),
		},

		async created() {
			this.swipeHeight = window.innerWidth || document.documentElement.clientWidth || document.body.clientWidth;
			window.scrollTo(0, 0);
			await this.loadGoodsInfo(this.$route.query.id);
			this.skuData.sku.price = this.goodsInfo.SpecialOffer;
			this.skuData.goodsInfo.id = this.goodsInfo.GoodsID;
			this.skuData.goodsInfo.title = this.goodsInfo.GoodsName;
			this.skuData.goodsInfo.picture = this.goodsInfo.Image;
			this.initSelectString();

			this.maxCount = this.goodsInfo.Quota > 0 ? this.goodsInfo.Quota : this.maxCount;
			this.maxCount = Math.min(this.maxCount, this.goodsInfo.Stock);
		},

		methods: {
			...mapActions(['loadGoodsInfo', 'loadShoppingCart']),

			initSelectString() {
				for (let i = 0; i < this.goodsInfo.ValuesList.length; i++) {
					this.selectString.push(this.goodsInfo.ValuesList[i][0].GoodsValueName);
					this.serialNumbers.push(this.goodsInfo.ValuesList[i][0].SerialNumber);
					this.selectIndex[i] = this.goodsInfo.ValuesList[i][0].GoodsValueID;
				}
				this.specString = this.selectString.join('-');
				this.serialString = this.serialNumbers.join('-');
			},

			select(index) {
				this.goodsInfo.ValuesList[index].forEach(item => {
					if (item.GoodsValueName === this.selectString[index]) {
						this.selectIndex[index] = item.GoodsValueID;
						this.serialNumbers[index] = item.SerialNumber;
					}
				});
				this.specString = this.selectString.join('-');
				this.serialString = this.serialNumbers.join('-');
			},

			toCart() {
				this.$router.push('/cart');
			},

			showSku() {
				this.isSkuShow = true;
			},

			async onAddCartClick() {
				await this.loadShoppingCart();
				let specsString = this.selectString.join('-');
				let inCart = false;
				for (let i = 0; i < this.shoppingCart.length; i++) {
					if (parseInt(this.shoppingCart[i].GoodsID) === parseInt(this.goodsInfo.goodsID) && this.shoppingCart[i].specs === specsString) {
						inCart = true;
						break;
					}
				}
				if (inCart) {
					Dialog.confirm({
						title: '提示',
						message: '该商品已在购物车中，是否继续添加？'
					}).then(async () => {
						await this.addToCart();
					}).catch(() => {});
				} else {
					await this.addToCart();
				}
			},

			async addToCart() {
				let specs = [];
				for (let i = 0; i < this.selectIndex.length; i++) {
					let tmp = {
						'CategoryType': i + 1,
						'GoodsValueID': this.selectIndex[i],
					};
					specs.push(tmp);
				}
				let res = await this.$request.addCart(this.goodsInfo.goodsID, this.count, specs);
				if (res) {
					this.$Message.success('加入购物车成功');
				}
				this.isSkuShow = false;
			},

		}
	};
</script>

<style lang="less" scoped>
	.goods {
		padding-bottom: 50px;
		&-tag {
			font-size: 12px;
			border-top: 1px solid #e5e5e5;
			span {
				margin-right: 10px;
			}
			i {
				color: red;
				margin-right: 3px;
			}
			img {
				width: 12px;
				margin-right: 3px;
				margin-top: 6px;
			}
		}
		&-title {
			line-height: 18px;
			padding-top: 10px;
			margin-bottom: 6px;
			font-size: 14px;
			color: #333;
			font-weight: 700;
			border-top: 1px solid #f0f0f0;
		}
		&-subtit {
			font-size: 13px;
			color: #333;
			line-height: 21px;
		}
		&-price {
			color: #f44;
			font-size: 20px;
		}
		&-market-price {
			text-decoration: line-through;
			margin-left: 8px;
			font-size: 13px;
			color: #999;
		}
		&-cell-group {
			margin: 15px 0;
			.van-cell__value {
				color: #999;
			}
		}
		&-info-title {
			height: 44px;
			line-height: 44px;
			text-align: center;
			font-size: 14px;
			font-weight: 700;
			margin: 10px;
			border-top: 1px solid #e5e5e5;
		}
		&-info p {
			margin: 0;
			padding: 0;
			margin-block-end: 0;
			margin-block-start: 0;
			display: grid;
		}
		&-info img {
			width: 100%;
		}
	}

	.item-select-title {
		color: #000000;
		font-size: 14px;
		margin-right: 15px;
	}

	.item-select-column {
		margin-top: 5px;
		display: flex;
		flex-direction: column;
	}

	.item-select-row {
		display: flex;
		flex-direction: row;
		margin-bottom: 8px;
	}
</style>


